
* {
  box-sizing: border-box;
}
*::before, *::after {
    box-sizing: border-box;
}
html,
body {
  height: 100%;
}
html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0;
  line-height: 1.42857;
  font-size: 14px;
  color: #444;
  font-family: Arial, sans-serif;
  background: #fff;
  position: relative;
}
h1, h2, h3, h4 {
  color: #444;
  line-height: 1.1;
}
body {
  background: #fff;
  padding: 0;
  margin: 0;
}



